const AvatarTrasitions = () => {
	const [imgKey, setImgKey] = useState('one');

	useEffect(() => {

		setInterval(() => {
			const items = ['one', 'two', 'three', 'four', 'five'];
			const item = items[Math.floor(Math.random() * items.length)];

			setImgKey(item);
		}, 3500);

		return false;

	}, []);

	return <Avatar size="2xl" src={`${imgKey}.jpg`} />
}

