import React, { useState } from 'react';
import {
  ChakraProvider,
  Box,
  Grid,
  HStack,
  VStack,
  PinInput,
  PinInputField,
  Text,
  Center,
  Avatar,
  theme,
} from '@chakra-ui/react';

import Main from './Main';

function App() {
  const [pin, setPin] = useState('');
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  const onPinInputChange = val => {
    let cake = val.toUpperCase();
    setPin(cake);
  }

  const onPinInputComplete = val => {
    let cake = val.toUpperCase();
    if ('CAKE' === cake) {
      setIsLoggedIn(true);
    }
  }

  return (
    <ChakraProvider theme={theme}>
      <Box bg="pink.100">
        <Grid minH="100vh" p={3}>

          <Center>
            {
              isLoggedIn ? <Main /> :
                <VStack>

                  <Avatar size="2xl" src="one.jpg" />

                  <Text fontSize="3xl">Happy Birthday Baby!!!</Text>

                  <HStack>
                    <PinInput value={pin} onChange={onPinInputChange} colorScheme="pink" placeholder="ğŸ°" size="lg" onComplete={onPinInputComplete} type="alphanumeric">
                      <PinInputField />
                      <PinInputField />
                      <PinInputField />
                      <PinInputField />
                    </PinInput>
                  </HStack>
                  <Text fontSize="lg">Type <code>CAKE</code> above</Text>

                </VStack>
            }
          </Center>

        </Grid>
      </Box>
    </ChakraProvider>
  );
}

export default App;
